html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

:root {
  --size: 16px;
  --base-font-size: 20px;
  --base-radius: #95a5a6;
}

@media screen and (max-width: 1200px) {
  :root {
    --base-font-size: 18px;
  }
}

@media screen and (max-width: 600px) {
  :root {
    --base-font-size: 16px;
  }
}

* {
  margin: 0;
  padding: 0;
  position: relative;
}

body {
  touch-action: pan-y;
}

body, input, textarea {
  font-family: colfax-web, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, sans-serif;
}

body {
  color: #000;
  background: #fcfcfd;
}

html, body, .jbx-button, .jbx-input {
  font-size: var(--base-font-size);
  line-height: 1;
}

.jbx-code-snippet {
  border-radius: var(--size);
  width: 100%;
  font-family: monaco, monospace;
  font-size: calc(var(--size)  - 3px);
  padding: var(--size) calc(var(--size) * 1.2);
  color: #34495e;
  resize: none;
  background: #ecf0f1;
  border: none;
  line-height: 1.33;
}

.jbx-code-snippet:focus {
  outline: none;
}

.jbx-code-area {
  border-radius: var(--size);
  width: 100%;
  font-family: monaco, monospace;
  font-size: calc(var(--size)  - 3px);
  padding: var(--size) calc(var(--size) * 1.2);
  color: #34495e;
  resize: none;
  min-height: 120px;
  background: #ecf0f1;
  border: none;
  line-height: 1.33;
}

.jbx-code-area:focus {
  outline: none;
}

.jbx-button {
  appearance: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  padding: 0 calc(var(--size) / 2);
  height: calc(var(--size) * 2.5);
  background-color: #eee;
  border: none;
  display: inline-block;
  box-shadow: 3px 3px #ccc;
}

.jbx-button:active {
  top: 1px;
  left: 1px;
  box-shadow: 2px 2px #ccc;
}

.jbx-a {
  color: #000;
  box-shadow: var(--accent-color) 0 1px 0, inset var(--accent-color) 0 -2px 0;
  font-weight: 500;
  text-decoration: none;
}

.jbx-a:hover {
  color: "#34495e";
}

.jbx-h1, .jbx-h2, .jbx-h3, .jbx-h4 {
  margin: 0;
  padding: 0;
  font-weight: 500;
  line-height: 1;
}

.jbx-h4 {
  font-size: calc(var(--base-font-size));
  letter-spacing: -.11px;
}

.jbx-h3 {
  font-size: calc(var(--base-font-size) * 1.25);
  letter-spacing: -.22px;
}

.jbx-h2 {
  font-size: calc(var(--base-font-size) * 1.5);
  letter-spacing: -.33px;
}

.jbx-h1 {
  font-size: calc(var(--base-font-size) * 2.5);
  letter-spacing: -1px;
}

.jbx-small-text {
  font-size: calc(var(--base-font-size) * .8);
  line-height: calc(var(--base-font-size) * .6);
}

.jbx-container {
  max-width: 1080px;
  padding: calc(var(--size) * 2.5) calc(var(--size) * 1.5) calc(var(--size) * 4);
  margin: 0 auto;
}

.jbx-box {
  flex: 1;
}

.jbx-inline {
  flex-flow: wrap;
  flex: 1;
  display: flex;
}

.jbx-stack {
  flex-flow: column wrap;
  flex: 1;
  display: flex;
}

.jbx-input {
  color: #777;
  -webkit-text-fill-color: #777;
  opacity: 1;
  appearance: none;
  height: calc(var(--size) * 2.5);
  padding: 0 calc(var(--size) * .5);
  background-color: #eee;
  border: none;
  border-radius: 0;
  flex: 1;
  display: block;
  box-shadow: inset 3px 3px #0000000a, inset .5px .5px #00000008;
}

.jbx-dropzone {
  height: 120px;
  max-width: 320px;
  text-align: center;
  color: #000;
  cursor: pointer;
  border: 2px dashed #000;
  flex-direction: column;
  flex: 1;
  place-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.jbx-dropzone:hover {
  border-color: #999;
}

.jbx-dropzone span {
  width: 100%;
  cursor: pointer;
}

.jbx-dropzone input {
  height: 100%;
  width: 100%;
  opacity: 0;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
}

.jbx-range {
  width: 100%;
  appearance: none;
  background-color: var(--accent-color);
  height: var(--size);
  border-radius: 0;
  flex: 1;
}

.jbx-range::-webkit-slider-thumb {
  appearance: none;
  box-shadow: none;
  height: calc(var(--size) * 2);
  width: var(--size);
  background-color: #000;
  border: none;
  border-radius: 0;
}

.jbx-range::-moz-range-thumb {
  appearance: none;
  box-shadow: none;
  height: calc(var(--size) * 2);
  width: var(--size);
  background-color: #000;
  border: none;
  border-radius: 0;
}

.jbx-range::-webkit-slider-runnable-track {
  appearance: none;
}

.jbx-range:focus {
  outline: none;
}

